.text {
  text-align: center;
  text-transform: uppercase;
  margin-top: 200px;
  color: #1f3282;
  font-size: 30px;
}
.btn {
  display: flex;
  justify-content: center;
}

span {
  color: #d51db8;
}

button[data-action='start'] {
  cursor: pointer;
  margin-right: 20px;
  padding: 20px 40px;
  border-radius: 2px;
  border: none;
  background: linear-gradient(#ffafbd, #ffc3a0);

  color: #d51db8;

  &:hover {
    background: linear-gradient(#ee9ca7, #ffdde1);
  }
}
button:disabled {
  background: lightgray;
  &:hover {
    background: lightgray;
  }
}
button[data-action='stop'] {
  cursor: pointer;

  padding: 20px 40px;
  border-radius: 2px;
  border: none;
  background: linear-gradient(#74f2ce, #7cffcb);

  color: #d51db8;

  &:hover {
    background: linear-gradient(#20bf55, #01baef);
  }
}
